.wpcf7 {

	.inline-form-els {
		display: flex;
		flex-direction: column;
		justify-content: center;
		padding: 20px 0;

		@include widerThan($tabletSM) {
			flex-direction: row;
			justify-content: space-between;
		}

		& > span {
			width: 100%;

			&:first-child {
				margin-bottom: 20px;
				margin-right: 0;

				@include widerThan($tabletSM) {
					margin-right: 20px;
					margin-bottom: 0;
				}
			}
		}
	}

	.newsletter-disclaimer {
		margin-top: 10px;
		display: block;
		font-size: 14px;
		color: #afafaf;
	}

	button,
	input[type="button"],
	input[type="reset"],
	input[type="submit"] {
		width: 100%;
	}
}
