html {
	box-sizing: border-box;
}

*,
*::before,
*::after {

	box-sizing: inherit;
}

body {
	background: $color__background-body;
}

blockquote,
q {
	quotes: "" "";

	&::before,
	&::after {
		content: "";
	}
}

hr {
	background-color: $color__background-hr;
	border: 0;
	height: 1px;
	margin-bottom: 1.5em;
}

@import "_lists";

img {
	height: auto;

	max-width: 100%;
}

@import "_tables";
