#masthead {
	padding: 0;
	z-index: 999;

	& > .content-width {
		display: grid;
		grid-template-columns: 1fr 1fr 1fr;

		@include widerThan($tabletLG) {
			grid-template-columns: 1fr 1fr;
			justify-content: center;
			align-items: flex-start;
			padding: 15px 0;
		}

		#site-navigation {
			order: 1;
			margin-top: 10px;

			@include widerThan($tabletLG) {
				order: 2;
				margin-top: 0;
			}
		}

		.site-branding {
			order: 2;
			padding-top: 10px;

			@include widerThan($tabletLG) {
				order: 1;
			}

			.logo {
				margin: 0;
				padding: 0;
				text-align: center;

				@include widerThan($tabletLG) {
					text-align: left;
				}

				a {
					border-bottom: 0;
					background-color: transparent;
					box-shadow: none;

					#logo-svg {
						max-width: 150px;
						max-height: 150px;
					}
				}

				&.logo--white {

					.logo-svg-use {
						--logo-color: #fff;
					}
				}

				&.logo--black {

					.logo-svg-use {
						--logo-color: #000;
					}
				}
			}
		}

		.cart-navigation {
			order: 3;
			margin-top: 10px;

			@include widerThan($tabletLG) {
				margin-top: 0;
			}
		}
	}
}
